/* font/montserrat */
// google webfont https://google-webfont/montserrat-helper.herokuapp.com/font/montserrat

/* montserrat-300italic - latin_cyrillic */
@font-face {
  font-family: 'Montserrat';
  font-style: italic;
  font-weight: 300;
  src: url('../font/montserrat/montserrat-v14-latin_cyrillic-300italic.eot'); /* IE9 Compat Modes */
  src: local('Montserrat Light Italic'), local('Montserrat-LightItalic'),
       url('../font/montserrat/montserrat-v14-latin_cyrillic-300italic.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
       url('../font/montserrat/montserrat-v14-latin_cyrillic-300italic.woff2') format('woff2'), /* Super Modern Browsers */
       url('../font/montserrat/montserrat-v14-latin_cyrillic-300italic.woff') format('woff'), /* Modern Browsers */
       url('../font/montserrat/montserrat-v14-latin_cyrillic-300italic.ttf') format('truetype'), /* Safari, Android, iOS */
       url('../font/montserrat/montserrat-v14-latin_cyrillic-300italic.svg#Montserrat') format('svg'); /* Legacy iOS */
}
