  line-height: 1.3;
  letter-spacing: -1px;
  margin-bottom: 55px;
  text-transform: uppercase; }

.h2, h2 {
  font-size: 1.875rem;
  line-height: 1.14;
  margin-bottom: 20px; }

.h3, h3 {
  font-size: 16px;
  line-height: 1.38; }

.h4, h4 {
  font-size: 14px;
  line-height: 1.38; }

.h5, h5 {
  font-size: 13px; }

.h6, h6 {
  font-size: 12px; }

small,
