    text-decoration: none;
    outline: none; }

a.link:hover, a.link:focus, a.link:active {
  text-decoration: none; }

img, .img {
  box-sizing: content-box;
  display: inline-block;
  vertical-align: middle; }

.container {
  margin: 0 auto;
  width: 100%;
  min-width: 320px;
  padding: 0 10px; }

/* text align */
.text-center, .text--center {
  text-align: center !important; }

.text-left, .text--left {
  text-align: left !important; }

.text-right, .text--right {
  text-align: right !important; }

/* loader */
.loader {
  width: 100%;
  height: 100vh;
  left: 0;
  top: 0;
  position: fixed;
  z-index: 10000;
  background-color: #ffffff;
  display: flex;
  align-items: center;
  justify-content: center; }

@keyframes bouncing-loader {
  to {
    opacity: 0.1;
    transform: translate3d(0, -1rem, 0); } }

.bouncing-loader {
  display: flex;
  justify-content: center; }
  .bouncing-loader > div {
    width: 1rem;
    height: 1rem;
    margin: 3rem 0.2rem;
    background: #8385aa;
    border-radius: 50%;
    animation: bouncing-loader 0.6s infinite alternate; }
    .bouncing-loader > div:nth-child(2) {
      animation-delay: 0.2s; }
    .bouncing-loader > div:nth-child(3) {
      animation-delay: 0.4s; }

/* img */
.img-responsive, .img--responsive {
  display: block;
  height: auto;
  max-width: 100%; }

.img-round, .img--round {
  border-radius: 50%; }

/* section */
.section {
  border-top: 5px solid #ffe500;
  padding: 45px 0 45px 0;
  min-width: 320px; }
  .section__head {
    text-align: center;
    text-transform: uppercase;
    margin-bottom: 55px; }
    .section__head--contacts {
      margin-bottom: 30px; }
    .section__head--tastes {
      margin-bottom: 15px; }
    .section__head > div {
      display: inline-block;
      padding: 0 35px;
      margin: 0 10px;
      position: relative; }
      .section__head > div::before, .section__head > div::after {
        position: absolute;
        height: 30px;
        width: 4px;
        background: #ffe500;
        content: '';
        top: 50%;
        transform: translateY(calc(-50% - 1px)); }
      .section__head > div::before {
        left: 0; }
      .section__head > div::after {
        right: 0; }
    .section__head h1, .section__head .h1, .section__head h2, .section__head .h2 {
      margin-bottom: 0;
      color: currentColor; }

.container .section {
  min-width: inherit; }

ul li {
  list-style-type: disc; }

ol li {
  list-style-type: decimal; }

/* bg */
.bg--pink {
  background-color: pink !important; }

.bg--white {
  background-color: #ffffff !important; }

.bg--black {
  background-color: #000000 !important; }

/* no padding no margin */
.no--p {
  padding: 0 !important; }
  .no--p-top {
    padding-top: 0 !important; }
  .no--p-bottom {
    padding-bottom: 0 !important; }
  .no--p-left {
    padding-left: 0 !important; }
  .no--p-right {
    padding-right: 0 !important; }

.no--m {
  margin: 0 !important; }
  .no--m-top {
    margin-top: 0 !important; }
  .no--m-bottom {
    margin-bottom: 0 !important; }
  .no--m-left {
    margin-left: 0 !important; }
  .no--m-right {
    margin-right: 0 !important; }

.no--b {
  background: none !important; }
  .no--b-image {
    background-image: none !important; }

.no--border {
  border: 0 !important; }

/* icons */
.icon {
  display: inline-block; }

/* color */
.color--black {
  color: #000000 !important; }

.color--white {
  color: #ffffff !important; }

.nowrap {
  white-space: nowrap !important; }

